.calc__input {
    max-width: 100px;
    height: 48px;
    opacity: 0.34;
    border: none;
    color: #202020;
    font-family: "TTFirsNeue-DemiBold";
    font-size: 18px;
    font-weight: 400;
    line-height: 18.72px;
    letter-spacing: 0.5px;
    margin-bottom: 61px;
    font-weight: bold;
    text-align: center;
}


@media screen and (max-width: 767px) and (min-width: 425px) {
    .calc__input {
        width: 76px;
    }
}

@media screen and (max-width: 424px) and (min-width: 375px) {
    .calc__input {
        width: 65px;
    }
}