.card__table-text {
    color: #202020;
    font-family: "TTFirsNeue-Regular";
    font-size: 13px;
    font-weight: 400;
    line-height: 18.72px;
    letter-spacing: 0.5px;
    font-weight: normal;  
}

td .card__table-text {
    border-spacing: 5px;
}

@media screen and (max-width: 1023px) and (min-width: 768px) {
    .card__table-text {
        font-size: 11px;
    }
}